* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  margin: 0;
  padding: 0;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

form#search-form {
  background: #5858e7;
  padding: 20px;
  text-align: center;
  margin-bottom: 50px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}

.gallery {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 150px;
}

.photo-card {
  flex: 0 0 23%;
  margin: 0 1%;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  margin-bottom: 2%;
  box-shadow: 1px 1px 4px 1px rgba(0, 0, 0, 0.1);
}

.photo-card img {
  display: block;
  max-width: 100%;
  object-fit: cover;
  height: 295px;
  width: 100%;
}
.info {
  display: flex;
  justify-content: space-between;
  padding: 0 15px;
}

.info-item {
  text-align: center;
}
.info-item span {
  display: block;
}

.load-more {
  display: none;
}
